import React from "react";
import Navigation from "../../components/Navigation";
import Footer from "../../components/Footer";
import ItemInfo from "../../components/ItemInfo";
import LightBox from "../../components/Lightbox";
import asset1 from "../../images/dji-5331597_1280.jpg";
import asset2 from "../../images/slider_1.jpg";
import asset3 from "../../images/slider_2.jpg";
import PrizeBtn from "../../components/PrizeBtn";
import data from "../../data";

const info = data.filter((item) => {
  if (item.route === "/agregaty-uprawowe-hydropak/talerz-lekki-armasz") {
    return item;
  }
});
const DiscLightArmasz = () => (
  <div>
    <Navigation />
    <div className="wrapper">
      <h1 className="product-title">{info[0].name}</h1>
      <div className="description">
        <ul className="long-list">
          <h3>Wyposaenie dodatkowe</h3>
          <li>
            <span>owietlenie</span>
          </li>
          <li>
            <span>piasty bezobsugowe typ skf</span>
          </li>
          <li>
            <span>ramki obciajce</span>
          </li>
          <li>
            <span>rozstaw 85cm(tzw. brona)</span>
          </li>
          <li>
            <span>skrobaki do wau</span>
          </li>
          <li>
            <span>talerze  560</span>
          </li>
          <li>
            <span>wa cewnikowy</span>
          </li>
          <li>
            <span>wa daszkowy  500</span>
          </li>
          <li>
            <span>wa daszkowy  600</span>
          </li>
          <li>
            <span>wa  500</span>
          </li>
          <li>
            <span>wa  600mm</span>
          </li>
          <li>
            <span>wa packera</span>
          </li>
          <li>
            <span>wa sprynowy</span>
          </li>
          <li>
            <span>wa zbkowany</span>
          </li>
          <li>
            <span>zgrzebo</span>
          </li>
          <li>
            <span>zmiana koloru</span>
          </li>
        </ul>
      </div>
      <div className="box">
        <ItemInfo
          className="table-info"
          rawNumber={3}
          colNumber={8}
          raw1={[
            "Typ",
            "ATL 115",
            "ATL 118",
            "ATL 120",
            "ATL 122",
            "ATL 125",
            "ATL 127",
            "ATL 130",
          ]}
          raw2={[
            "Szeroko robocza [m]",
            "1,5",
            "1,8",
            "2,0",
            "2,2",
            "2,5",
            "2,7",
            "3,0",
          ]}
          raw3={["Waga [kg]", "560", "630", "660", "730", "770", "890", "970"]}
        />
        <PrizeBtn />
        <div className="assets">
          <LightBox assets={[asset1, asset2, asset3]} />
        </div>
      </div>
    </div>

    <Footer />
  </div>
);

export default DiscLightArmasz;
